import PropTypes from 'prop-types';

// @mui
import { Box, Grid, Stack, Typography, useTheme } from '@mui/material';

// third-party
import { keys, includes } from 'lodash';

// project
import PageContainer from 'layouts/PageContainer';
import PageHeader from 'layouts/PageHeader';
import MainCard from 'components/cards/MainCard';

const TypoBlock = (props) => {
  const theme = useTheme();
  const { typoName, typoObject, sx } = props;
  return (
    <Box
      sx={{
        display: 'grid',
        gridTemplateColumns: 'repeat(7, 1fr)',
        gridAutoFlow: 'row',
        alignItems: 'center',
        width: 1,
        maxWidth: 800,
        // height: 78,
        maxHeight: 200,
        p: 2,
        bgcolor: theme.palette.mode === 'dark' ? 'grey.600' : 'grey.300',
        color: theme.palette.text.default,
        borderRadius: `${theme.shape.borderRadius}px`,
        ...typoObject,
        ...sx
      }}
    >
      <Typography
        variant="inherit"
        sx={{ gridColumn: '1/2', gridRow: '1 / 2' }}
      >
        ABC
      </Typography>
      <Typography
        variant="inherit"
        sx={{ gridColumn: '2/3', gridRow: '1 / 2' }}
      >
        {typoName}
      </Typography>
      <Typography
        variant="inherit"
        sx={{ gridColumn: '4/20', gridRow: '1 / 2' }}
      >
        {typoObject.fontSize} {' / '}
        {typoObject.fontWeight} {' / '}
        {typoObject.lineHeight}
      </Typography>
    </Box>
  );
};

TypoBlock.propTypes = {
  typoName: PropTypes.string,
  typoObject: PropTypes.object,
  sx: PropTypes.oneOfType([
    PropTypes.arrayOf(
      PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool])
    ),
    PropTypes.func,
    PropTypes.object
  ])
};

const UITypography = () => {
  const theme = useTheme();
  const mainTypo = ['h1', 'h2', 'h3', 'h4', 'h5', 'h6'];
  const otherTypo = [
    'subtitle1',
    'subtitle2',
    'body1',
    'body2',
    'caption',
    'overline'
  ];
  return (
    <PageContainer>
      <PageHeader
        title="Typography"
        subtitle="Typography provides a set of type sizes that work well together, and also with the layout grid."
      />
      {/* <p>
                `
                h1 h2 h3  Heading text
                h4        Page Headings
                h5        Section headings
                h6        Card Headings
                Body1     Components and section headings
                body2     Heavily used in most components
                Subtitle1 Components
                Subtitle2 Labels
                Caption   Helper text
                Overline  Subtitles
                `
            </p> */}
      <Grid container>
        <Grid item xs={12} sx={{ alignItems: 'center' }}>
          <MainCard>
            <Stack
              direction={'row'}
              justifyContent={'space-around'}
              spacing={2}
              mt={5}
            >
              <Stack spacing={2}>
                {keys(theme.typography).map(
                  (typo, index) =>
                    includes(mainTypo, typo) && (
                      <TypoBlock
                        key={index}
                        typoName={typo}
                        typoObject={theme.typography[typo]}
                      />
                    )
                )}
                {keys(theme.typography).map(
                  (typo, index) =>
                    includes(otherTypo, typo) && (
                      <TypoBlock
                        key={index}
                        typoName={typo}
                        typoObject={theme.typography[typo]}
                      />
                    )
                )}
              </Stack>
            </Stack>
          </MainCard>
        </Grid>
      </Grid>

    </PageContainer>
  );
};

export default UITypography;
